<template>
<div class="login-page">
    <div class="cy-login-header">
        <span class="cy-login-title">{{$t('Login.Title')}}</span>
        <span class="cy-login-description">{{$t('Login.Description')}}</span>
    </div>
    <div class="cy-login-body">
        <ol class="cy-login-list">
            <li class="cy-list-item">
                {{$t("Login.Setting.KR")}}<br>{{$t("Login.Setting.EN")}}
            </li>
        </ol>
        <RadioInput v-model="language" :options="[{name: $t(`Login.${$CYconstants.LANGUAGE.KOREA}`), value: $CYconstants.LANGUAGE.KOREA}, {name: $t(`Login.${$CYconstants.LANGUAGE.ENGLISH}`), value: $CYconstants.LANGUAGE.ENGLISH}]" :name="'languageSelector'"></RadioInput>
        <ol class="cy-login-list margin-top-20" start="2">
            <li class="cy-list-item">
                {{$t("Login.Action.KR")}}<br>{{$t("Login.Action.EN")}}
            </li>
        </ol>
        <TextInput :label="$t('Login.Email')" v-model="FormData.userId" v-validate="'required|email'" :name="'email'"></TextInput>
        <TextInput :label="$t('Login.Password')" isPassword v-model="FormData.userPw" v-validate="'required'" :name="'password'"></TextInput>
        <Button :label="$t('Login.Login')" primary @click.native="doLogin"></Button>
        <router-link to="/auth/find-password-or-id" class="cy-login-find">{{$t("Login.Find")}}</router-link>
        <Button :label="$t('Login.Join')" default @click.native="doJoin"></Button>
    </div>
</div>
</template>

<script>
import RadioInput from '@/components/Common/Input/RadioInput';
import TextInput from '@/components/Common/Input/TextInput';
import Button from '@/components/Common/Button/Button';
import {LOGIN} from '@/mutations/Auth.mutation';
import {SHOW_POPUP_BUTTON, HIDE_POPUP_BUTTON} from '@/mutations/Common.mutation';

export default {
    name: 'Login',
    components: {
        RadioInput,
        TextInput,
        Button,
    },
    props: {},
    data: function () {
        return {
            language: this.$i18n.locale.toUpperCase(),
            FormData: {
                userId: 'test01@naver.com',
                userPw: 'init00',
            }
        }
    },
    methods: {
        doLogin: async function () {
            let isValid = await this.$validator.validateAll();
            if (!isValid) {
                let error = this.errors.items[0];
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.msg);
            } else {
                try {
                    await this.$store.dispatch(LOGIN, {...this.FormData});
                } catch (err) {
                    this.$store.dispatch(SHOW_POPUP_BUTTON, err.message);
                }
            }
        },
        doJoin: function () {
            this.$router.push('/joining/step-one');
        },
    },
    watch: {
        language: function (value) {
            this.$i18n.locale = value.toLowerCase();
            this.$validator.localize(value.toLowerCase());
            this.$StoreService.set('locale', value.toLowerCase());
        }
    }
}
</script>
